import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/layout/Home'
import SideBar from '@/layout/SideBar'
import ceshi from '@/components/nav'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/SideBar',
      name: 'SideBar',
      component: SideBar
    },
    {
      path: '/ceshi',
      name: 'ceshi',
      component: ceshi
    }
  ]
})



